.Wrapper {
  padding: 50px 0 100px 0;
  width: 100%;
}
.Container {
  max-width: 1140px;
  margin: 0 auto;
}
.TittleBox {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  opacity: 0.95;
  z-index: 1;
  background-color: rgba(31, 136, 210, 0.9);
  height: 100px;
}
.BgImg {
  background-size: cover;
  z-index: 2;
  background-repeat: no-repeat;
  background-image: url('../../img/bg_other.png');
  background-position: center center;
}
.Tittle {
  color: #fff;
  display: flex;
  width: 100%;
  justify-content: center;
  font-size: 30px;
  text-transform: uppercase;
  position: relative;
  font-weight: 700;
}

.Tittle:after {
  display: block;
  position: absolute;
  z-index: 2;
  content: '';
  height: 2px;
  background: -webkit-linear-gradient(top, #1f88d2, #1f88d2);
  width: 100%;
  top: -42px;
}

.Tittle:before {
  z-index: 1;
  display: block;
  position: absolute;
  top: 80px;
  content: '';
  height: 2px;
  background: -webkit-linear-gradient(top, #1f88d2, #1f88d2);
  width: 100%;
}
.PreiseList {
  padding: 0;
  padding-top: 50px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.PreiseItem {
  display: flex;
  flex-direction: column;
  width: 265px;
  border-radius: 5px;
  box-shadow: 0px 30px 40px rgba(75, 86, 127, 0.25);
  transition: 0.2s;
  margin-bottom: 25px;
}
.PreiseItem:hover {
  transform: scale(1.05);
}
.ListName {
  background: -webkit-linear-gradient(top, #009e2c, #009e2c);
  color: #ffff;
  border-radius: 5px 5px 0px 0;
}
.ListName h2 {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  text-transform: none;
}
.ListName h3 {
  display: flex;
  justify-content: center;
}
.ListName p {
  display: flex;
  justify-content: center;
  font-weight: 500;
}
.PriseDescription {
  align-items: center;
  padding: 20px 0 20px 0;
}
.PriseDescription p {
  display: flex;
  justify-content: center;
  align-items: center;
}

.PriseDescription button {
  display: flex;
  justify-content: center;
  margin: auto;
  width: 200px;
  padding: 10px;
  border: none;
  color: #ffff;
  font-weight: 600;
  background: #be3531;
  border: solid 1.5px #be3531;
  transition: 0.8s;
  margin-bottom: 5px;
}
.PriseDescription button:hover {
  opacity: 0.6;
  cursor: pointer;
}
@media screen and (max-width: 1023px) {
  .Container {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .Wrapper {
    padding: 0 0 100px 0;
  }
  .TittleBox {
    height: 125px;
  }
  .PreiseList {
    flex-direction: column;
  }
  .PreiseItem {
    margin: 0 auto;
    margin-bottom: 30px;
  }
  .Tittle {
    text-align: center;
    font-size: 24px;
  }
  .Tittle:after {
    display: none;
  }
  .Tittle:before {
    display: none;
  }
}
